<template>
  <v-container
    style="
      z-index: 99;
      position: relative;
      width: 100%;
      border-top: 5px solid #4d7ca6;
      height: 40px;
    "
  >
    <nav>
      <div v-on:click="openMobileNav()" id="burger">
        <div class="line1"></div>
        <div class="line2"></div>
        <div class="line3"></div>
      </div>
      <ul class="nav-links">
        <li>
          <a href="/home"> Inicio </a>
        </li>

        <li>
          <a href="/about"> Quiénes somos </a>
        </li>
        <li>
          <a href="/services"> Servicios </a>
        </li>
        <li>
          <a href="/contact"> Contáctanos </a>
        </li>
        
      </ul>
      <v-spacer></v-spacer>
        <v-btn icon to="/login">
          <v-icon color="white">mdi-export</v-icon>
        </v-btn>
    </nav>
  </v-container>
</template>

<script>
export default {
  name: "Navbar",
  navLinks: [
    {
      name: "en1",
      link: "xcxc",
      dropdown: false,
    },
    {
      name: "en2",
      link: "xcxc",
      dropdown: false,
    },
    {
      name: "en3",
      link: "xcxc",
      dropdown: false,
    },
  ],
  methods: {
    openMobileNav() {
      const burger = document.getElementById("burger");
      const nav = document.querySelector(".nav-links");
      const navLinks = document.querySelectorAll(".nav-links li");
      // Toggle navigation on mobile
      nav.classList.toggle("nav-active");
      // Burger toggler
      burger.classList.toggle("toggle");

      // Animate navigation links
      navLinks.forEach((link, index) => {
        if (link.style.animation || link.style.webkitAnimation) {
          nav.style.display = "none";
          link.style.animation = "";
          link.style.webkitAnimation = "";
        } else {
          nav.style.display = "block";
          link.style.webkitAnimation = `navLinkFade 0.5s ease forwards ${
            index / 7
          }s`;
          link.style.animation = `navLinkFade 0.5s ease forwards ${index / 7}s`;
        }
      });
    },
    openDropdownNav() {
      const dropdownLink = document.querySelectorAll(".dropdown-link");
      dropdownLink.forEach((dropdown) => {
        dropdown.addEventListener("mouseover", () => {
          dropdown.children[1].style.display = "block";
        });
        dropdown.addEventListener("mouseleave", () => {
          dropdown.children[1].style.display = "none";
        });
      });
    },
    /* Run only on mobile
       
       This code below here will change the behavior of mobile menu links.
       when you click for once it will open the drop down & by the
       second click it will lead you to the link page if you don't
       want to go in dropdown links
    */
    countClicksOnMobileDropdown() {
      const dropdownLink = document.querySelectorAll(".dropdown-link");
      dropdownLink.forEach((dropdown) => {
        let counts = dropdown.clicks || 0;
        dropdown.addEventListener("click", () => {
          counts++;
          if (counts % 2 == 0) {
            window.location.href = dropdown.children[0].getAttribute("href");
          } else {
            event.preventDefault();
            dropdown.children[1].style.display = "block";
            setTimeout(() => {
              dropdown.children[1].style.display = "none";
            }, 5000);
          }
        });
        setTimeout(() => {
          counts = 0;
        }, 8000);
      });
    },
  },
  mounted() {
    this.openDropdownNav();
    if (window.innerWidth < 768) {
      this.countClicksOnMobileDropdown();
    }
  },
};
</script>

<style>
nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 100%;
  height: 10px;
  max-width: 100%;
  min-width: 100%;
  width: 100%;
}

div#logo {
  letter-spacing: 5px;
  color: #fefefe;
  font-weight: 800;
  font-size: 2rem;
}

ul.nav-links {
  display: flex;
  width: 100%;
  line-height: 75px;
}

ul.nav-links li {
  list-style: none;
  margin-right: 15px;
}

ul.nav-links a {
  text-decoration: none;
  color: #fefefe;
  font-size: 1.2rem;
  font-weight: 500;
  display: block;
  box-shadow: #966d6d;
}

#burger {
  display: none;
  cursor: pointer;
}

#burger div {
  width: 20px;
  height: 3px;
  margin: 4px;
  background-color: #fefefe;
  transition: all 0.3s ease-in;
}

ul.dropdown-menu {
  position: absolute;
  top: 8vh;
  background-color: #966d6d;
  min-width: 140px;
  cursor: pointer;
  display: none;
}

ul.dropdown-menu li:first-child {
  margin: 0 0 10px 0;
}

ul.dropdown-menu li {
  margin: 10px 0;
}

ul.dropdown-menu li:last-child {
  margin: 10px 0 0 0;
}

ul.dropdown-menu a {
  line-height: 8vh;
  padding: 5px 15px;
  background-color: #f1d1d1;
  line-height: 50px;
}

/* Tablet */
@media screen and (max-width: 1024px) {
  ul.nav-links {
    width: 100%;
  }
 
}

/* Mobile */
@media screen and (max-width: 772px) {
 
  ul.nav-links {
    display: none;
    position: absolute;
    flex-direction: column;
    width: 100%;
    height: 650%;
    top: 6vh;
    right: 0;
    padding: 10%;
    align-items: center;
    justify-content: flex-start;
    background-color: #25425e;
    opacity: 0.8;
    transform: translateX(110%);
    transition: transform 0.5s ease-in;
  }

  ul.nav-links li {
    opacity: 0;
    margin-right: 10px;
  }
  ul.nav-links a {
    width: 100%;
  }

  div#burger {
    display: block;
  }

  ul.dropdown-menu {
    position: relative;
    top: 0;
  }
}

.nav-active {
  transform: translateX(0) !important;
}

@keyframes navLinkFade {
  from {
    opacity: 0;
    transform: translateX(-60px);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

.toggle .line1 {
  transform: rotate(-45deg) translate(-3px, 5px);
}

.toggle .line2 {
  opacity: 0;
}

.toggle .line3 {
  transform: rotate(45deg) translate(-6px, -7px);
}
</style>
