<template>
  <div>
    <v-tooltip top>
      <template v-slot:activator="{ on, attrs }">
        <v-speed-dial
          style="margin-right: 70px"
          class="mb-2"
          bottom
          color="success"
          dark
          fab
          fixed
          right
          direction="left"
          v-model="fab"
        >
          <template v-slot:activator>
            <v-btn
              v-model="fab"
              v-bind="attrs"
              v-on="on"
              large
              color="success"
              dark
              fab
            >
              <v-icon v-if="fab"> mdi-close </v-icon>
              <v-icon v-else> mdi-whatsapp </v-icon>
            </v-btn>
          </template>
          <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                @click="gotoLink('store')"
                fab
                dark
                small
                color="secondary"
                v-bind="attrs"
                v-on="on"
              >
                <v-icon>mdi-store</v-icon>
              </v-btn>
            </template>
            <span>Ir al catalogo</span>
          </v-tooltip>

          <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                @click="gotoLink('email')"
                fab
                dark
                small
                color="primary"
                v-bind="attrs"
                v-on="on"
              >
                <v-icon>mdi-email</v-icon>
              </v-btn>
            </template>
            <span>Enviar mensaje</span>
          </v-tooltip>
        </v-speed-dial>
      </template>
      Whatsapp
    </v-tooltip>
    <v-card
      flat
    >
 <div
       :style="{
          backgroundImage:
            'url(' + require('../../assets/images/fondo_navbar2.jpg') + ')',
          height: '100%',
          width: '100%',
          position: 'absolute',
          opacity: '0.9',
        }"
 >

 </div>
      <div
        :style="{
          backgroundImage:
            'url(' + require('../../assets/images/gradient.png') + ')',
          height: '100%',
          width: '100%',
          position: 'absolute',
          opacity: '0.4',
        }"
      ></div>
      <v-toolbar color="transparent" dark extended flat>
        <v-row style="margin-top: 35px">
          <v-col cols="3" lg="3" md="3" sm="5">
            <div class="md-toolbar-section-start mt-2">
              <img class="logo" contain />
            </div>
          </v-col>
          <v-spacer></v-spacer>
          <v-col cols="9" lg="3" md="3" sm="4" class="mb-6">
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-btn icon color="primary" dark v-bind="attrs" v-on="on">
                  <v-icon style="color: black">mdi-phone</v-icon>
                </v-btn>
              </template>
              <span>{{ phone }}</span>
            </v-tooltip>
            <v-btn :href="this.facebook" target="_blank" class="mx-4" dark icon>
              <v-icon style="color: black" size="24px">mdi-facebook</v-icon>
            </v-btn>
            <v-btn
              :href="this.instagram"
              target="_blank"
              class="mx-4"
              dark
              icon
            >
              <v-icon style="color: black" size="24px">mdi-instagram</v-icon>
            </v-btn>
          </v-col>
        </v-row>
      </v-toolbar>
      <NavItem style="margin-top: -30px; background: #25425e"> </NavItem>

      <Slider v-if="this.$route.name == 'Home'"> </Slider>
      <Banner v-else> </Banner>
    </v-card>
  </div>
</template>
<script>
import NavItem from "@/components/Default/NavItem";
import Slider from "@/components/Home/Slidernew";
import Banner from "@/components/Default/Bannerx";
export default {
  components: {
    NavItem,
    Slider,
    Banner,
  },
  data: () => ({
    fab: false,
    phone: "",
    facebook: "",
    instagram: "",
    whatsapp: "",
    catalogue: "",
  }),
  mounted() {
    this.getNumberSocials();
  },
  methods: {
    getNumberSocials() {
      this.$http
        .get(`/listar-infoempresa`)
        .then((res) => {
          if (res.data.business[0].phone != null) {
            this.phone = res.data.business[0].phone;
            this.facebook = res.data.business[0].socials.facebook;
            this.instagram = res.data.business[0].socials.instagram;
            this.whatsapp = res.data.business[0].socials.whatsapp;
          }
        })
        .finally();
    },
    gotoLink(ruta) {
      if (ruta == "email") {
        window.open(this.whatsapp);
      } else {
        window.open(this.catalogue);
      }
    },
  },
};
</script>
<style>
/* DESKTOP */
@media all and (max-width: 999999999px) and (min-width: 1340px) {
  img.logo {
    content: url("../../assets/images/visionlogoH.svg");
    width: 50%;
  }
  div.cardnavbar {
    background-color: brown;
    width: 100%;
    height: 100%;
    position: absolute;
  }
}
/* =TABLET */
@media all and (max-width: 1339px) and (min-width: 600px) {
  img.logo {
    content: url("../../assets/images/visionlogoH.svg");
  }
}
/* MOBILE */
@media all and (max-width: 599px) and (min-width: 100px) {
  img.logo {
    content: url("../../assets/images/visionlogoV.svg");
    width: 140%;
  }
}
</style>